/* Google Fonts */

/* Main CSS */

html,
body {
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
}


/*  color suggestions

\ default: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
blue: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
*/
.main {
  max-width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
.back_hindrence {
  width: 100%;
  height: 100%;
  background-color: #a7c4cc;
  position: fixed;
  background-position: fixed;
  z-index: 500;
}
.main_parent {
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
  font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", sans-serif;
}
.main_child {
  overflow: hidden;
  min-height: 80vh;
  padding: 10px;
  z-index: 999;
  min-width: 290px;
  max-width: 310px;
  background-color: white;

  text-align: center;
  border-radius: 15px;
}
/* cloud type design */

.cloud_main {
  position: relative;
}
.cloud {
  width: 350px;
  height: 200px;
  background: #eceff1;
  position: absolute;
  top: -100px;
  left: 0px;
  border-radius: 50%;
  z-index: -1;
}
.cloud_main .cloud:nth-child(even) {
  position: absolute;
  width: 300px;
  height: 300px;
  top: -10rem;
}
.cloud_main .cloud:nth-child(odd) {
  width: 300px;
  height: 300px;
  left: 60%;
  top: -50px;
}
/* top content */
.head_container,
.head_container > div {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding-inline: 5px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
/* headings */
.content_container {
  margin-top: -5px;
}
.content_main {
  font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", sans-serif;
  padding: 0px 10px 0px 10px;
}
.content_head {
  font-size: 28px;
}
.content_desc {
  font-weight: 500;
  min-width: 100%;
  margin: 5px 0px 5px 0px;
  color: rgb(182, 177, 177);
  font-size: 14px;
  margin-top: -1rem;
}
.button_main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 2rem;
}
#btn {
  border-radius: 2em !important;
  width: 35%;
  background-color: #2b63e6;
  font-weight: 600;
  font-size: 14px;
  text-transform: capitalize;
}
.btn_2 {
  background-color: transparent !important;
  color: black !important;
  border: 2px solid black !important;
}
.Bottom_main > p {
  font-weight: 500;
  color: grey;
}

